.header
{
    position: sticky;
    top:0;
    min-height: 100px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
    background-color: black;
    z-index: 1;
}

.header-links
{
    display: flex;
}

.header-links ul li
{
    list-style: none;
    display: inline-block;

}

.header-links ul li a{
    text-decoration: none;
    text-transform: uppercase;
    color: white;
    padding: 0pc 15px;
}

h1
{
    color: white;
    font-size: 30px;
}

.cartlength
{
    margin:3px;
    font-size: 14px;
    font-weight: 700;
    vertical-align: super;
    color: yellow;
}