.cart-items
{
    margin:30px auto;
    width: 50%;
    background-color: orange;
    border: 2px solid black;
    border-radius: 5px;

}

.cart-items-header
{
    display: flex;
    justify-content:space-around ;
    align-items: center;
    height: 50px;
    border-bottom: 2px solid black;
    font-size: 20px;
}

.cart-items-empty
{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    font-weight: 700;
    padding-top: 20px;
    padding-bottom: 20px;
}

.cart-items-image
{
    width:100px;
    height: 100px;
    border:2px solid black;
    margin-right: 10px;
}

.cart-items-list
{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-top: 20px;
    padding-left: 20px;
    padding-bottom: 20px;
    font-size: 20px;
    font-weight: 700;
    margin-left: 5%;
}

.cart-items-name,
.cart-items-function
{
    width: 25%;
}

.cart-item-remove
{
    width: 80px;
    margin-right: 15px;
    font-weight: 700;
    border:none;
    border-radius: 3px;
    font-size: 18px;
    color: white;
    cursor: pointer;
    background-color: green;
}

.cart-items-remove:active
{
    transform: translateY(1.2px);
}



.cart-items-total-price
{
    display: flex;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 22%;
    font-size: 20px;
    font-weight: 700;
    border-top: 2px solid black;

}

.cart-items-total-price
{
    display: flex;
    padding-left: 45%;

}

.clear-cart
{
    text-align: right;
}

.clear-cart-button
{
    border:none;
    border-radius: 5px;
    background-color: red;
    height: 30px;
    width: 100px;
    font-size: 18px;
    color: white;
    cursor: pointer;
}

.clear-cart-button:hover
{
    background-color: green;
}