.img
{
    height: 350px;
    width: 220px;
    border:2px solid rebeccapurple
}

.img:hover
{
    transform: scale(1.1);
}

.products
{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    margin-top: 30px;
    margin-bottom: 20px;
  
}

.product-name
{
    margin-top: 15px;
}
.button
{
    margin-bottom: 60px;
    margin-top: 10px;
    height: 30px;
    width: 90px;
    border-radius: 10px;

}

.button:hover
{
    background-color: hotpink;
    cursor: pointer;
}

.price
{
    margin-top: 10px;
}